<template>
  <v-layout v-if="fullList.length > 0" row class="pt-2 mt-2 mr-2 ml-2">
    <v-flex xs12 class="">
      <v-card v-for="combatant in fullList" :key="combatant.name" class="mb-3 mt-2">

        <v-toolbar dense card height="25px"
         :class="calculateToolbarClass(combatant) ? 'green white--text' :'grey lighten-2'">
          <v-badge left v-if="combatant.isPlayer">
            <span slot="badge"><strong>PC</strong></span>
          {{ combatant.name }}
          </v-badge>
          <span v-else>
            {{ combatant.name }}
          </span>
          <v-spacer></v-spacer>
          <v-btn :class="calculateToolbarClass(combatant) ? 'white--text' : 'black--text'"
           icon small v-if="isEdgeOfList(combatant) != -1" @click="moveCombatantUp(combatant)">
            <v-icon>vertical_align_top</v-icon>
          </v-btn>
          <v-btn :class="calculateToolbarClass(combatant) ? 'white--text' : 'black--text'"
           small icon v-if="isEdgeOfList(combatant) != 1" @click="moveCombatantDown(combatant)">
            <v-icon>vertical_align_bottom</v-icon>
          </v-btn>
        </v-toolbar>

        <v-card-text class="pb-0 pt-0">
          <v-layout align-center row class="pt-0 mt-0 pb-0 mb-0">
            <v-flex xs2 class="mr-3">
              <v-text-field min="0" v-model="combatant.int"
               type="number" label="Initiative" @click="sortList()">
            </v-text-field>
            </v-flex>

            <v-flex xs2>
              <v-text-field min="0" v-model="combatant.ac"
               type="number" label="AC" @click="sortList()">
            </v-text-field>
            </v-flex>

            <v-flex xs8>
            </v-flex>

            <!-- Actions for a combatant -->
            <v-flex xs1>
              <v-btn icon small class="red white--text" @click="removeCombatant(combatant)">
                <v-icon>delete</v-icon>
              </v-btn>
            </v-flex>

          </v-layout>
        </v-card-text>
      </v-card>
    </v-flex>
    </v-layout>
</template>

<script>
export default {
  data: () => {
    return {}
  },

  props: {
    fullList: {
      type: Array
    },
    turn: {
      type: Number
    }
  },

  methods: {
    calculateToolbarClass(combatant) {
      if (this.turn == this.fullList.indexOf(combatant)) {
        return true
      } else {
        return false
      }
    },

    sortList() {
      this.$emit('sort')
    },

    removeCombatant(combatant) {
      this.$emit('removeCombatant', combatant)
    },

    isEdgeOfList(combatant) {
      if (this.fullList.indexOf(combatant) == 0) {
        return -1
      } else if (this.fullList.indexOf(combatant) == this.fullList.length -1) {
        return 1
      } else {
        return 0
      }
    },
    
    moveCombatantDown(combatant) {
      this.$emit('moveCombatantDown', combatant)
    },

    moveCombatantUp(combatant) {
      this.$emit('moveCombatantUp', combatant)
    }
  }
}
</script>

